// External dependencies
import jQuery from 'jquery';

// Wordpress dependencies
import apiFetch from '@wordpress/api-fetch';

jQuery( document ).ready( () => {
	jQuery( '.post-type-coca_bais form#post' ).on( 'submit', ( ) => {
		// Saved data from browser storage.
		const savedOptions = window.localStorage.getItem( 'coca_bais_data' );

		if ( !! savedOptions && !! JSON.parse( savedOptions ) ) {
			apiFetch( { path: '/coca-bais/v1/save-post-meta', method: 'post', data: JSON.parse( savedOptions ) } );
		}

		// Just land here.
		// event.preventDefault();
	} );
} );
